.coffee {
  padding: 35px;
  background: rgb(61,168,193) /* Old browsers */;
  background: -moz-linear-gradient(left, rgba(61,168,193,0.9) 0%, rgba(47,100,178,1) 61%)/* FF3.6-15 */;
  background: -webkit-linear-gradient(left, rgba(61,168,193,0.9) 0%,rgba(47,100,178,0.9) 61%), url('../images/coffeebk.jpg') no-repeat/* Chrome10-25,Safari5.1-6 */;
  background: linear-gradient(to right, rgba(61,168,193,0.9) 0%,rgba(47,100,178,0.9) 61%), url('../images/coffeebk.jpg') no-repeat/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3da8c1', endColorstr='#2f64b2',GradientType=1 )/* IE6-9 */;
  color: $white;
  background-size: 100%;
  @include mq('tablet') {
    padding: 80px 0;
    background: rgb(61,168,193) /* Old browsers */;
    background: -moz-linear-gradient(left, rgba(61,168,193,0.9) 0%, rgba(47,100,178,1) 61%)/* FF3.6-15 */;
    background: -webkit-linear-gradient(left, rgba(61,168,193,0.9) 0%,rgba(47,100,178,0.9) 61%), url('../images/coffeebk.jpg') no-repeat/* Chrome10-25,Safari5.1-6 */;
    background: linear-gradient(to right, rgba(61,168,193,0.9) 0%,rgba(47,100,178,0.9) 61%), url('../images/coffeebk.jpg') no-repeat/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3da8c1', endColorstr='#2f64b2',GradientType=1 )/* IE6-9 */;
    color: $white;
    background-size: 100%; } }

.coffee__cups {
  display: none;
  color: $white;
  text-align: center;
  position: relative;
  @include mq('tablet') {
    display: block; } }
.coffee__cups i {
  font-size: 30px; }
.coffee__cups p {
  text-align: center; }



.coffee__cups .coffee__cups--cup:nth-child(odd) {
  margin-top: 20px; }
.coffee__cups--cup:nth-child(even) {
  margin-top: -30px; }
